@charset "UTF-8";
/*************************************
Include Functions and Mixins
**************************************/
/*************************************
Include Variables
**************************************/
/*************************************
Background Color Names
**************************************/
/*************************************
Spacing
*************************************/
:root {
  --global-width: 1160px;
  --bp-medium: 641px;
  --bp-large: 1025px;
  --bp-xlarge: 1441px;
  --bp-xxlarge: 1921px;
  /*************************************
  Defaults
  **************************************/
  --default-border-radius: 6px;
  --default-input-border-radius: 20px;
  --default-input-min-height: 40px;
  --default-transition-speed: 0.2s;
  --default-transition-easing: ease-in-out;
  --default-box-shadow: 0 10px 10px -10px rgba(var(--color-rgb-black), 0.5);
  /*************************************
  z-index
  **************************************/
  --z-root: 0;
  --z-header: calc(var(--z-root) + 10);
  --z-above-all: calc(var(--z-header) + 10);
  /*************************************
  Header
  **************************************/
  --header-height-sm: 60px;
  --header-height-md: 70px;
  --header-height-lg: 80px;
  --header-transition-speed: calc(var(--default-transition-speed) * 2);
  /*************************************
  Layout
  **************************************/
  --grid-column-gutter: 3rem;
  /*************************************
  Colors
  **************************************/
  --color-black: #000;
  --color-blue: #145ca1;
  --color-blue-dark: #0e4274;
  --color-red: #98143c;
  --color-gray: #d4d7da;
  --color-white: #fff;
  --color-rgb-black: 0, 0, 0;
  --color-rgb-white: 255, 255, 255;
  --error-text: var(--color-red);
  --color-twitter: #79ccf0;
  --color-facebook: #547bbd;
  --color-linkedin: #1385c8;
  /*************************************
  Buttons
  *************************************/
  --button-primary-normal-bg-color: var(--color-blue);
  --button-primary-normal-border-color: var(--color-blue);
  --button-primary-normal-text-color: var(--color-white);
  --button-primary-hover-bg-color: var(--color-blue-dark);
  --button-primary-hover-border-color: var(--color-blue-dark);
  --button-primary-hover-text-color: var(--color-white);
  --button-primary-disabled-bg-color: rgba(var(--color-rgb-black), 0.4);
  --button-primary-disabled-border-color: transparent;
  --button-primary-disabled-text-color: rgba(var(--color-rgb-white), 0.7);
  --button-secondary-normal-bg-color: transparent;
  --button-secondary-normal-border-color: var(--color-black);
  --button-secondary-normal-text-color: var(--color-black);
  --button-secondary-hover-bg-color: var(--color-blue-dark);
  --button-secondary-hover-border-color: var(--color-blue-dark);
  --button-secondary-hover-text-color: var(--color-white);
  --button-secondary-disabled-bg-color: transparent;
  --button-secondary-disabled-border-color: rgba(var(--color-rgb-black), 0.4);
  --button-secondary-disabled-text-color: rgba(var(--color-rgb-black), 0.4);
  /*************************************
  Tables
  *************************************/
  --table-border-color: var(--color-gray);
  /*************************************
  Blocks
  *************************************/
  --block-padding-sm-top: 4.25rem;
  --block-padding-sm-bottom: 4.25rem;
  --block-padding-md-top: 6.25rem;
  --block-padding-md-bottom: 6.25rem;
  --block-padding-lg-top: 6.25rem;
  --block-padding-lg-bottom: 6.25rem;
  /*************************************
  Typography
  **************************************/
  --font-arial: Arial, sans-serif;
  --font-primary: var(--font-arial);
  --font-weight-thin: 100;
  --font-weight-light: 300;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --body-font-size: 1.6rem;
  --body-font-line-height: 1.5;
  --body-font: normal normal var(--font-weight-regular) var(--body-font-size) /
  	var(--body-font-line-height) var(--font-primary);
  --body-font-color: var(--color-black);
  --h1-color: var(--body-font-color);
  --h2-color: var(--body-font-color);
  --h3-color: var(--body-font-color);
  --h4-color: var(--body-font-color);
  --h5-color: var(--body-font-color);
  --h6-color: var(--body-font-color);
  --anchor-link-normal-color: var(--color-blue);
  --anchor-link-hover-color: var(--color-blue-dark);
  /*************************************
  Forms
  **************************************/
  --form-checkbox-radio-size: 16px;
  --form-checkmark-img: url("../../media/images/check-blue.png");
  --form-radio-fill-color: var(--color-black);
  --form-control-input-border: 2px solid var(--color-gray);
  --form-control-disabled-opacity: 0.4;
}
@media only screen and (min-width: 641px) {
  :root {
    --grid-column-gutter: 4rem;
  }
}

/*************************************
Include Libraries
**************************************/
/**
 *	Set base font size to 10px
 *	This makes calculating rem values very easy
 *	Just divide your pixel value by 10
 *	Need an rem value that matches 13px? 1.3rem
 *	Need an rem value that matches 27px? 2.7rem
 */
:root {
  font-size: 10px;
}

/**
 *	Reset element margins
 */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
  margin: 0;
}

h1 + * {
  margin-top: 2rem;
}

h2 + * {
  margin-top: 2rem;
}

h3 + * {
  margin-top: 2rem;
}

h4 + * {
  margin-top: 2rem;
}

h5 + * {
  margin-top: 1.5rem;
}

h6 + * {
  margin-top: 1.5rem;
}

p + * {
  margin-top: 1.5rem;
}

ol + * {
  margin-top: 1.5rem;
}

ul + * {
  margin-top: 1.5rem;
}

* + h1 {
  margin-top: 3rem;
}

* + h2 {
  margin-top: 3rem;
}

* + h3 {
  margin-top: 3rem;
}

* + h4 {
  margin-top: 3rem;
}

* + h5 {
  margin-top: 3rem;
}

* + h6 {
  margin-top: 3rem;
}

/*
 *	Global element styling
 */
body {
  font: var(--body-font);
  color: var(--body-font-color);
}

p {
  font: var(--body-font);
  color: var(--body-font-color);
}

blockquote {
  font: var(--body-font);
  color: var(--body-font-color);
}

h1 {
  font: normal normal var(--font-weight-bold) 3.6rem/1.21 var(--font-primary);
  color: var(--h1-color);
  text-transform: uppercase;
}
@media only screen and (min-width: 1025px) {
  h1 {
    font-size: 4.8rem;
  }
}

h2 {
  font: normal normal var(--font-weight-bold) 3.6rem/1.22 var(--font-primary);
  color: var(--h2-color);
  text-transform: uppercase;
}

h3 {
  font: normal normal var(--font-weight-medium) 2.6rem/1.25 var(--font-primary);
  color: var(--h3-color);
}

h4 {
  font: normal normal var(--font-weight-medium) 1.8rem/1.44 var(--font-primary);
  color: var(--h4-color);
}

h5 {
  font: normal normal var(--font-weight-medium) 1.6rem/1.5 var(--font-primary);
  color: var(--h5-color);
}

h6 {
  font: normal normal var(--font-weight-medium) 1.2rem/1.66 var(--font-primary);
  color: var(--h6-color);
}

/**
 *	Text size modifiers - usually used with paragaph elements
 */
.large {
  font-size: calc(var(--body-font-size) + 0.2rem);
}

small,
.small {
  font-size: calc(var(--body-font-size) - 0.2rem);
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.button,
.wysiwyg button,
input[type=submit],
.mce-button-primary {
  position: relative;
  display: inline-block;
  margin: 0;
  padding: 0.8rem 1.5rem;
  min-width: 160px;
  min-height: var(--default-input-min-height);
  border: 2px solid var(--button-primary-normal-border-color);
  font: normal normal var(--font-weight-bold) var(--body-font-size)/1.45 var(--font-primary);
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--button-primary-normal-text-color);
  background-color: var(--button-primary-normal-bg-color);
  box-shadow: var(--default-box-shadow);
  appearance: none;
  outline: none;
  cursor: pointer;
  transition: all var(--default-transition-speed) ease-in-out;
}
.button:hover, .button:active, .button.active,
.wysiwyg button:hover,
.wysiwyg button:active,
.wysiwyg button.active,
input[type=submit]:hover,
input[type=submit]:active,
input[type=submit].active,
.mce-button-primary:hover,
.mce-button-primary:active,
.mce-button-primary.active {
  color: var(--button-primary-hover-text-color);
  background-color: var(--button-primary-hover-bg-color);
  border-color: var(--button-primary-hover-border-color);
  text-decoration: none;
}
.button:disabled, .button.disabled,
.wysiwyg button:disabled,
.wysiwyg button.disabled,
input[type=submit]:disabled,
input[type=submit].disabled,
.mce-button-primary:disabled,
.mce-button-primary.disabled {
  pointer-events: none;
  cursor: not-allowed;
  color: var(--button-primary-disabled-text-color);
  background-color: var(--button-primary-disabled-bg-color);
  border-color: var(--button-primary-disabled-border-color);
}
.button.button--secondary,
.wysiwyg button.button--secondary,
input[type=submit].button--secondary,
.mce-button-primary.button--secondary {
  color: var(--button-secondary-normal-text-color);
  background-color: var(--button-secondary-normal-bg-color);
  border: 2px solid var(--button-secondary-normal-border-color);
}
.button.button--secondary:hover, .button.button--secondary:active, .button.button--secondary.active,
.wysiwyg button.button--secondary:hover,
.wysiwyg button.button--secondary:active,
.wysiwyg button.button--secondary.active,
input[type=submit].button--secondary:hover,
input[type=submit].button--secondary:active,
input[type=submit].button--secondary.active,
.mce-button-primary.button--secondary:hover,
.mce-button-primary.button--secondary:active,
.mce-button-primary.button--secondary.active {
  color: var(--button-secondary-hover-text-color);
  background-color: var(--button-secondary-hover-bg-color);
  border-color: var(--button-secondary-hover-border-color);
}
.button.button--secondary:disabled, .button.button--secondary.disabled,
.wysiwyg button.button--secondary:disabled,
.wysiwyg button.button--secondary.disabled,
input[type=submit].button--secondary:disabled,
input[type=submit].button--secondary.disabled,
.mce-button-primary.button--secondary:disabled,
.mce-button-primary.button--secondary.disabled {
  color: var(--button-secondary-disabled-text-color);
  background-color: var(--button-secondary-disabled-bg-color);
  border-color: var(--button-secondary-disabled-border-color);
}

.button--has-icon {
  min-width: 0;
  padding-left: 5.5rem;
}
.button__icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -2px;
  bottom: -2px;
  left: -2px;
  width: var(--default-input-min-height);
  background: var(--color-black);
  color: var(--color-white);
}
.button__icon {
  width: calc(var(--default-input-min-height) / 3);
  height: calc(var(--default-input-min-height) / 3);
  color: var(--color-white);
}
.button--slim {
  min-width: 125px;
  min-height: 0;
  padding: 0.4rem 1.5rem;
}

/*************************************
WordPress Specifics
**************************************/
.alignleft,
.alignright {
  display: inline-block;
}

.alignleft {
  float: left;
  margin: 0 20px 5px 0;
}

.alignright {
  float: right;
  margin: 0 0 5px 20px;
}

.alignnone {
  margin: 5px 15px 15px 0;
}

.aligncenter {
  display: block;
  margin: 15px auto;
}

.wp-caption {
  background: rgba(var(--color-rgb-black), 0.1);
  border: 1px rgba(var(--color-rgb-black), 0.3) solid;
  padding: 15px;
  font-size: 0.85em;
  text-align: center;
}
.wp-caption img {
  border: 1px rgba(var(--color-rgb-black), 0.3) solid;
  margin: 0 auto 15px;
  max-width: 98.5%;
  width: auto;
}

@media only screen and (max-width: 782px) {
  body #wpadminbar {
    position: fixed !important;
  }
}

/*************************************
Main Project Files
**************************************/
:root {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin-top: var(--header-height-sm);
}
@media only screen and (min-width: 641px) {
  body {
    margin-top: var(--header-height-md);
  }
}
@media only screen and (min-width: 1025px) {
  body {
    margin-top: var(--header-height-lg);
  }
}
body.expand-main-content, body.expand-footer {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - var(--header-height-sm));
}
@media only screen and (min-width: 641px) {
  body.expand-main-content, body.expand-footer {
    min-height: calc(100vh - var(--header-height-md));
  }
}
@media only screen and (min-width: 1025px) {
  body.expand-main-content, body.expand-footer {
    min-height: calc(100vh - var(--header-height-lg));
  }
}
body.expand-main-content .main-content {
  flex-grow: 1;
}
body.expand-footer .site-footer {
  flex-grow: 1;
}

@media screen and (max-device-width: 641px) {
  body {
    -webkit-text-size-adjust: 100%;
  }
}
img {
  max-width: 100%;
  height: auto;
}

button {
  background-color: transparent;
  border: 0;
  padding: 0;
}

ul {
  padding: 0;
}
nav ul {
  list-style: none;
  list-style-position: inside;
}

table {
  color: var(--body-font-color);
}
table thead tr th {
  border-bottom: 1px solid var(--table-border-color);
  padding: 0.5rem;
  text-align: left;
}
table tbody tr td {
  border-bottom: 1px solid var(--table-border-color);
  padding: 0.5rem;
  text-align: left;
}

blockquote {
  margin: 2rem 1rem;
}
blockquote::before {
  content: "“";
}
blockquote::after {
  content: "”";
}
blockquote p {
  font: inherit;
  display: inline;
  color: inherit;
}

code {
  background: var(--color-black);
  color: var(--color-white);
  padding: 0.1rem 0.25rem;
}

strong {
  font-weight: var(--font-weight-semibold);
}

video,
iframe {
  max-width: 100%;
}

pre {
  color: var(--body-font-color);
  white-space: normal;
}

.hide {
  display: none !important;
}

body {
  margin: 0 20px !important;
  background-color: #fff;
}